//FONTS
@mixin fontpath($name, $name1, $file) {
	// src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff")
	src: url('../fonts/' + $file +  '.eot');
	src: url('../fonts/' + $file +  '.eot?#iefix') format('embedded-opentype'), url('../fonts/' + $file +  '.woff2') format('woff2'), url('../fonts/' + $file +  '.woff') format('woff'), url('../fonts/' + $file +  '.ttf')  format('truetype'); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$familyPrimary: 'Glober';
$familyHelvetica: 'Helvetica Neue';

@font-face {
	@include fontdefault(normal, 400, $familyHelvetica);
	@include fontpath($familyHelvetica + ' Regular', $familyHelvetica + '-Regular', HelveticaNeue); }

@font-face {
	@include fontdefault(normal, 500, $familyHelvetica);
	@include fontpath($familyHelvetica + ' Medium', $familyHelvetica + '-Medium', HelveticaNeue-Medium); }

@font-face {
	@include fontdefault(normal, 400, $familyPrimary);
	@include fontpath($familyPrimary + ' Regular', $familyPrimary + '-Regular', fontfabric_-_globerregular); }
@font-face {
	@include fontdefault(normal, 600, $familyPrimary);
	@include fontpath($familyPrimary + ' Semibold', $familyPrimary + '-Semibold', fontfabric_-_globersemibold); }
@font-face {
	@include fontdefault(normal, 700, $familyPrimary);
	@include fontpath($familyPrimary + ' Bold', $familyPrimary + '-Bold', fontfabric_-_globerbold); }
