.hero {
	&__container {
		position: relative;
		margin: 0 auto;
		max-width: 1366px;
		@include cover; }
	&__content {
		display: table;
		width: 100%; }
	&__content-in {
		display: table-row; }
	&__info {
		display: table-cell;
		vertical-align: middle;
		padding: 40px;
		height: 420px; }
	&__title {
		max-width: 350px;
		color: $gray;
		&:not(:last-child) {
			margin-bottom: 40px; } } }
